import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    currentuser: null
  },
  getters: {
    getCurrentUser: state => {
      return state.currentuser
    }
  },
  mutations: {
    setCurrentUser: (state, payload) => {
      state.currentuser = payload
    },
    setNewPassword: (state, payload) => {
      state.currentuser.password = payload
    },
    setNewPhone: (state, payload) => {
      state.currentuser.phone = payload
    },
    setJoined: (state) => {
      state.currentuser.organizations.push({
        name: state.currentuser.invited.name,
        lastlogin: 'Never',
        invitedby: state.currentuser.invited.invitedby
      });
      state.currentuser.invited.value = false
    },
    setAccountSuspended: (state, payload) => {
      state.currentuser.suspended.value = payload[0]
      state.currentuser.suspended.for = payload[1]
      state.currentuser.suspended.time = Math.floor(+new Date / 1000)
    }
  },
  actions: {
    async setCurrentUser({
      commit
    }, payload) {
      await axios
        .get('../api/users/' + payload + ".json")
        .then(res => commit('setCurrentUser', res.data))
    }
  },
  modules: {}
})