<template>
  <div class="container">
    <input
      class="password-input"
      :type="inputType"
      @input="emitData($event.target.value)"
      v-model="password"
      :class="{'input-border-red': error}"
      @keydown.enter="$emit('entersubmit')"
      @focus="$emit('focus')"
      @blur="$emit('blur')"
    />
    <img class="show-icon" src="@/assets/show.svg" @click="showPassword" />
    <div class="tooltip-arrow" v-if="tooltip"></div>
    <div class="tooltip" v-if="tooltip">
      <h4 class="header">{{ tooltiptitle }}</h4>
      <div class="body">
        <ul>
          <li v-for="(tip, i) in tooltipbody" :key="i">{{tip}}</li>
        </ul>
      </div>

      <div class="labels">
        <h4>Strength:</h4>
        <p></p>
        <p>{{ passwordstrengthword }}</p>
      </div>
      <div class="bar">
        <div class="bar-bg"></div>
        <div
          :class="{weak: passwordstrength === 0, medium: passwordstrength === 1, strong: passwordstrength === 2}"
        ></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    prefilled: {
      type: String,
      default: ""
    },
    error: {
      type: Boolean,
      defualt: false
    },
    tooltip: {
      type: Boolean,
      deafult: false
    }
  },
  data() {
    return {
      show: false,
      inputType: "password",
      password: this.prefilled
    };
  },
  computed: {
    tooltiptitle() {
      let pass = this.password;
      if (pass === pass.toLowerCase() || pass.length < 5) {
        return "Some suggestions for your password:";
      } else {
        return "You have a strong password";
      }
    },
    tooltipbody() {
      let tips = [];
      let pass = this.password;
      if (pass === pass.toLowerCase()) {
        tips.push("Must have uppercase");
      }
      if (pass.length < 5) {
        tips.push("Add another word or two. Uncommon words are better.");
      }
      return tips;
    },
    passwordstrength() {
      let pass = this.password;

      if (pass === pass.toLowerCase() && pass.length < 5) {
        return 0;
      } else if (pass === pass.toLowerCase() || pass.length < 5) {
        return 1;
      } else {
        return 2;
      }
    },
    passwordstrengthword() {
      let str = this.passwordstrength;
      if (str === 0) {
        return "Weak";
      } else if (str === 1) {
        return "Medium";
      } else {
        return "Strong";
      }
    }
  },
  methods: {
    showPassword() {
      if (this.show === false) {
        this.show = true;
        this.inputType = "text";
      } else {
        this.inputType = "password";
        this.show = false;
      }
    },
    emitData(value) {
      this.$emit("password", value);
      5;
    }
  }
};
</script>

<style lang="scss" scoped>
.container {
  position: relative;

  .password-input {
    width: 373px;
    height: 50px;
    border-radius: 4px;
    border: solid 1px $mercury-60;
    outline: none;
    padding-left: 16px;

    font-family: LucidaGrande;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.43;
    letter-spacing: normal;
    color: $outer-space-50;
  }
  .input-border-red {
    border: solid 1.5px $error-50;
  }

  &:focus {
    border: solid 1px $sushi-50;
  }
}

.show-icon {
  position: absolute;
  margin-left: -40px;
  margin-top: 18.5px;
}
.tooltip-arrow {
  position: absolute;
  left: 385px;
  top: 15px;
  width: 10px;
  height: 10px;
  background-color: $mercury-10;
  z-index: 2;
  transform: rotate(45deg);
  border-bottom: solid 1px $mercury-50;
  border-left: solid 1px $mercury-50;
}
.tooltip {
  position: absolute;
  left: 390px;
  top: 0;
  width: 290px;

  border-radius: 4px;
  box-shadow: 0 6px 11px -1px rgba(0, 0, 0, 0.09);
  border: solid 1px $mercury-50;
  background-color: $mercury-10;

  padding: 16px 25px;
  h4 {
    @include Framewrk-Heading-04-Default-28353D;
  }
  p {
    @include Framewrk-Body-02-Default-28353D;
  }
  li {
    @include Framewrk-Body-02-Default-Profile-Dropdown-53626B;
  }
  ul {
    padding: 0 0 0 18px;
    margin: 8px 0 16px 0;
  }
  .labels {
    display: grid;
    grid-template-columns: 75px auto 50px;
    margin-bottom: 8px;
  }
  .bar {
    position: relative;
    .bar-bg {
      height: 3px;
      background-color: $outer-space-50;
      opacity: 0.09;
      border-radius: 3px;
    }
    .weak {
      background-color: $error-50;
      border-radius: 3px;
      width: 10%;
      height: 3px;
      position: absolute;
      top: 0;
    }
    .medium {
      width: 50%;
      border-radius: 3px;
      height: 3px;
      background-color: $important-50;
      position: absolute;
      top: 0;
    }
    .strong {
      width: 100%;
      border-radius: 3px;
      height: 3px;
      background-color: $sushi-50;
      position: absolute;
      top: 0;
    }
  }
}
</style>