<template>
  <div>
    <BackgroundImages logoPosition="center"></BackgroundImages>
    <div @click="logout">
      <LogOut></LogOut>
    </div>
    <h1>Choose Organization</h1>
    <OrganizationBox
      class="organization-box"
      v-for="(org, i) in getCurrentUser.organizations"
      v-bind:key="i"
      :name="org.name"
      :login="org.lastlogin"
    ></OrganizationBox>
  </div>
</template>

<script>
import BackgroundImages from "@/components/BackgroundImages.vue";
import LogOut from "@/components/LogOut.vue";
import OrganizationBox from "@/components/OrganizationBox.vue";
import { mapGetters, mapMutations } from "vuex";
export default {
  name: "pickorganization",
  components: {
    BackgroundImages,
    LogOut,
    OrganizationBox
  },
  computed: {
    ...mapGetters(["getCurrentUser"])
  },
  methods: {
    ...mapMutations(["setCurrentUser"]),
    logout() {
      this.setCurrentUser(null);
      this.$router.push("/login");
    }
  }
};
</script>

<style lang="scss" scoped>
h1 {
  font-family: Heebo;
  font-size: 26px;
  font-weight: 800;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.31;
  letter-spacing: normal;
  text-align: center;
  color: $mercury-0;
  margin-bottom: 30px;
}
.organization-box {
  margin-bottom: 12px;
}
.logout-container {
  cursor: pointer;
}
</style>