<template>
  <div>
    <BackgroundImages logoPosition="left"></BackgroundImages>
    <div class="center-text">
      <div
        class="countdown"
      >{{ doubleDigits(Math.floor(this.time / 3600)) }} : {{ doubleDigits(Math.floor(this.time / 60)) }} : {{ doubleDigits(this.time % 60) }}</div>
      <h1>Account Suspended</h1>
      <p>
        At vero eos et accusamus et iusto odio dignissimos
        <br />ducimus qui blanditiis praesentium voluptatum deleniti.
      </p>
      <div class="button-container">
        <div class="button" @click="submitReactivate" :class="{ success: time === 0 }">Reactivate</div>
      </div>
    </div>
  </div>
</template>

<script>
import BackgroundImages from "@/components/BackgroundImages.vue";
import { mapGetters } from "vuex";
export default {
  name: "checkemail",
  data() {
    return {
      time: 10
    };
  },
  components: {
    BackgroundImages
  },
  computed: {
    ...mapGetters(["getCurrentUser"])
  },
  created() {
    let timenow = Math.floor(+new Date() / 1000);
    let user = this.getCurrentUser.suspended;

    let timeremaining = user.time - timenow + user.for;

    if (timeremaining <= 0) {
      this.time = 0;
    } else {
      this.time = timeremaining;
    }

    this.Timer();
  },
  methods: {
    Timer() {
      if (this.time > 0) {
        setTimeout(() => {
          this.time -= 1;
          this.Timer();
        }, 1000);
      }
    },
    submitReactivate() {
      if (this.time === 0) {
        this.$router.push("reactivateaccount");
      }
    },
    doubleDigits(value) {
      let t = value.toString();
      if (t.length < 2) {
        t = "0" + t;
      }
      return t;
    }
  }
};
</script>

<style lang="scss" scoped>
.center-text {
  text-align: center;
  width: 608px;
  .countdown {
    font-family: Heebo;
    font-size: 48px;
    font-weight: 300;
    font-stretch: normal;
    font-style: normal;
    line-height: 0.9;
    letter-spacing: normal;
    color: $mercury-0;
    margin-bottom: 50px;
  }
  h1 {
    font-family: Heebo;
    font-size: 26px;
    font-weight: 800;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.31;
    letter-spacing: normal;
    color: $mercury-0;
    margin-bottom: 20px;
  }
  p {
    font-family: Heebo;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.43;
    letter-spacing: normal;
    color: $mercury-0;
    margin-bottom: 50px;
  }
  .button-container {
    display: flex;
    justify-content: center;
    .button {
      border-radius: 4px;
      padding: 8px 17px;
      text-align: center;
      font-family: Heebo;
      font-size: 14px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.29;
      letter-spacing: 0.1px;
      background-color: $mercury-30;
      color: $mercury-50;
      cursor: default;
    }
    .success {
      background-color: $sushi-60;
      color: $mercury-0;
      cursor: pointer;
    }
  }
}
</style>